import { useContext } from 'react';

import styles from './OverlayFinished.module.css';

import GameContext from '../../contexts/Game';

const OverlayFinished = () => {

    const {
        completed
    } = useContext(GameContext);

    if (!completed) return null;

    return <div className={styles.container}>
        <div className={styles.innerContainer}>
            <h1>Parabns!</h1>
            <h3>Voc se lembrou de todos os estados do Brasil, compartilhe este jogo com seus amigos para desafi-los.</h3>
            <h5>Conhea mais sobre o desenvolvedor deste game: <a target='_blank' href="https://github.com/manbomb">@manbomb</a></h5>
        </div>
        <div className={styles.overlay}></div>
    </div>;
};

export default OverlayFinished;